import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Row from 'react-bootstrap/Row';
import Alert from 'react-bootstrap/Alert';
import axios from '../axios.js';

function SMTPForm() {
    const [smtpSettings, setSmtpSettings] = useState({});
    const [validated, setValidated] = useState(false);
    const [loading, setLoading] = useState(false);

    const getSmtpSettings = async () => {

        /*await Promise.resolve("hi")
            .then(setSmtpSettings(smtpSettings));*/
        axios.get('admin/settings')
            .then( (res) => {
                console.log(res.data);
                setSmtpSettings(res.data.smtp_setup);
            })
    }

    useEffect(() => {
        getSmtpSettings();
    }, [])

    const handleChange = (e) => {
        console.log(e);
        const value =
            e.target.name === "tls" || e.target.name === "authentication"
                ? e.target.checked : e.target.value;
        console.log(e.target.name, value);
        setSmtpSettings({
            ...smtpSettings,
            [e.target.name]: value
        })
    }
    const [updateSuccess, setUpdateSuccess] = useState(false);
    const [updateFailed, setUpdateFailed] = useState(false);

    const handleSubmit = async (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            console.log(smtpSettings);
            event.stopPropagation();
            window.alert("Please check all fields")
        } else {
            setValidated(true);
            setUpdateFailed(false);
            setUpdateSuccess(false);
            setLoading(true);
            console.log(smtpSettings);
            let settings = {
                host: smtpSettings.host,
                port: smtpSettings.port,
                authentication: smtpSettings.authentication,
                tls: smtpSettings.tls
            };
            if (settings.authentication === true) {
                settings = {
                    ...settings,
                    userName : smtpSettings.userName,
                    password : smtpSettings.password
                }
            } else {
                settings = {
                    ...settings,
                    userName: "",
                    password: ""
                }
            }
            axios.post('admin/settings', settings )
                .then( (res) => {
                    console.log(res);
                    if (res.status === 200) {
                        setUpdateSuccess(true);
                        setUpdateFailed(false);
                        setLoading(false);
                    } else {
                        throw new Error("Invalid Settings");
                    }
                })
                .catch(() => {
                    setUpdateFailed(true);
                    setUpdateSuccess(false);
                    setLoading(false);
                });
            console.log(updateSuccess);
            console.log(updateFailed);

        }
    };

    return (
        <React.Fragment>
            <Alert show={updateFailed} variant="danger" onClose={() => setUpdateFailed(false)} dismissible>
                <p>
                    Could not connect with the SMTP server. Please check all the fields and try again.
                </p>
            </Alert>

            <Alert show={updateSuccess} variant="success" onClose={() => setUpdateSuccess(false)} dismissible>
                <p>
                    SMTP settings were updated successfully!
                </p>
            </Alert>


        <Form noValidate validated={validated} onSubmit={handleSubmit}>

            <Form.Group as={Row} md="4" className="p-2" controlId="validationCustom01">
                <Form.Label column md={2}>Host</Form.Label>
                <Col md={4}>
                    <InputGroup hasValidation>
                        <Form.Control
                            required
                            type="text"
                            placeholder="Host Address"
                            name="host"
                            defaultValue={smtpSettings.host}
                            onChange={handleChange}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter a valid host address.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
            </Form.Group>

            <Form.Group as={Row} md="4" className="p-2" controlId="validationCustom02">
                <Form.Label column md={2}>Port</Form.Label>
                <Col md={4}>
                    <InputGroup hasValidation>
                        <Form.Control
                            required
                            type="number"
                            placeholder="Port"
                            name="port"
                            defaultValue={smtpSettings.port}
                            onChange={handleChange}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter a valid port number.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
            </Form.Group>

            <Form.Group as={Row} md="4" className="p-2" controlId="tls">
                <Col md={{ span: 4, offset: 2 }}>
                    <Form.Check
                        type="checkbox"
                        id="tls"
                        label="Enable TLS"
                        name="tls"
                        defaultChecked={smtpSettings.tls}
                        onClick={handleChange}
                    />
                </Col>
            </Form.Group>

            <Form.Group as={Row} md="4" className="p-2" controlId="authentication">
                <Col md={{ span: 4, offset: 2 }}>
                    <Form.Check
                        type="switch"
                        id="authentication"
                        label="Authentication"
                        name="authentication"
                        checked={smtpSettings.authentication}
                        onChange={handleChange}
                    />
                </Col>
            </Form.Group>

            <Form.Group as={Row} md="4" className="p-2" controlId="validationCustomUsername" hidden={!smtpSettings.authentication}>
                <Form.Label column md={2}>Username</Form.Label>
                <Col md={4}>
                    <InputGroup hasValidation>
                        <Form.Control
                            required={smtpSettings.authentication}
                            type="text"
                            placeholder="Username"
                            name="userName"
                            defaultValue={smtpSettings.userName}
                            onChange={handleChange}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter username.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
            </Form.Group>

            <Form.Group as={Row} md="4" className="p-2" controlId="validationCustom03" hidden={!smtpSettings.authentication}>
                <Form.Label column md={2}>Password</Form.Label>
                <Col md={4}>
                    <InputGroup hasValidation>
                        <Form.Control
                            required={smtpSettings.authentication}
                            type="password"
                            placeholder="Password"
                            name="password"
                            defaultValue={smtpSettings.password}
                            onChange={handleChange}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter password.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
            </Form.Group>

            



                <Button className="" type="submit" disabled={loading }>Save SMTP Setup</Button>
        </Form>
        </React.Fragment>
    );
}

function LandingPageSetup() {
    const [client, setClient] = useState({});
    const [validated, setValidated] = useState(false);
    const getClient = async () => {
        /*let client = DummyData.client;*/
            axios.get('admin/settings')
                .then((res) => {
                    setClient({ name: res.data.clientName });
                })


    }

    useEffect(() => {
        getClient();
    }, [])

    const handleChange = (e) => {
        console.log(e);
        const { name, value } = e.target;
        console.log(e.target.name, value);
        setClient({
            ...client,
            [name]: value
        })
    }

    const [updateSuccess, setUpdateSuccess] = useState(false);
    const [updateFailed, setUpdateFailed] = useState(false);

    const handleSubmit = async (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        } else {
            setValidated(true);
            console.log(client);

            setUpdateSuccess(false);
            setUpdateFailed(false);
            axios.post('admin/settings/updateLandingPage', {clientName : client.name})
                .then((res) => {
                    console.log(res.body);
                    if (res.status === 200) {
                        setUpdateSuccess(true);
                        setUpdateFailed(false);
                    } else {
                        throw new Error("Invalid Settings");
                    }
                })
                .catch(() => {
                    setUpdateFailed(true);
                    setUpdateSuccess(false);
                });
            console.log(updateSuccess);
            console.log(updateFailed);
        }

    };
    const [resetting, setResetting] = useState(false);
    const resetMailbox = async () => {
        setResetting(true);
        const confirm = window.prompt("Do you really want to reset?This will permanently delete all of the previous data. Type 'RESET' to continue");

        if (confirm === 'RESET') {
            axios.post('admin/settings/resetMailbox', {})
                .then((res) => {
                    console.log("Reset Response", res.body);
                    if (res.status === 200) {
                        window.alert("All mails were deleted successfully.");
                        setResetting(false);
                    } else {
                        window.alert("An error occured. Please try again later.");
                        setResetting(false);
                    }
                })
        } else {
            setResetting(false);
        }

    }

    return (
        <React.Fragment>
            <Alert show={updateFailed} variant="danger" onClose={() => setUpdateFailed(false)} dismissible>
                <p>
                    Could not update settings. Please try again later.
                </p>
            </Alert>

            <Alert show={updateSuccess} variant="success" onClose={() => setUpdateSuccess(false)} dismissible>
                <p>
                    Landing Page details updated successfully!
                </p>
            </Alert>
        <Form noValidate validated={validated} onSubmit={handleSubmit}>

            <Form.Group as={Row} md="4" className="p-2" controlId="validationCustom01">
                <Form.Label column md={2}>Company Name</Form.Label>
                <Col md={4}>
                    <InputGroup hasValidation>
                        <Form.Control
                            required
                            type="text"
                            placeholder="Company Name"
                            name="name"
                            defaultValue={client.name}
                            onChange={handleChange}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter Company Name
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
            </Form.Group>
            <Button className="" type="submit">Update Name</Button>
            <Button
                className="ms-4"
                variant="outline-danger"
                disabled={resetting}
                onClick={resetMailbox}
            >Reset Mailbox
            </Button>
            </Form>
        </React.Fragment>
        )
}

function Settings() {
    return (
        <React.Fragment>
            <h2 className="me-md-auto link-dark text-decoration-none fs-4">
                <i className="bi bi-sliders p-2"></i>
                Settings
            </h2>
            <hr />
            <div className="m-4">
                <h4> SMTP Setup </h4>
                <SMTPForm />
                <hr />
                <h4> Landing Page </h4>
                <LandingPageSetup />
                
            </div>
        </React.Fragment>
    )
}



export default Settings;