import React from 'react';
import { useState, useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Alert from 'react-bootstrap/Alert';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import { CSVLink} from "react-csv";
import axios from '../axios.js';

function SentTable() {
    const [sentMails, setSentMails] = useState([{}]);
    const getSentMails = async () => {
        axios.get('admin/sentMails')
            .then((res) => {
                setSentMails(res.data);
            });
    }
    useEffect(() => {
        getSentMails()
    }, [])

    return (
        <React.Fragment>
            <CSVLink
                data={sentMails.map((mail) => ({
                    From: mail.from,
                    Recipient: mail.recipient_email,
                    Subject: mail.subject,
                    Sent_Date: (new Date(mail.sent_date)).toLocaleDateString(),
                    Sent_Time: (new Date(mail.sent_date)).toLocaleTimeString()
                })
                )}
                filename="Sent_mails.csv"
            >
                <Button variant="secondary" className="ms-2 mb-2">
                    <i className="bi bi-file-earmark-arrow-down pe-1"></i>Export as CSV
                </Button>
            </CSVLink>

            <Table striped bordered hover >
                <thead>
                    <tr>
                        <th>From</th>
                        <th>Recipient</th>
                        <th>Subject</th>
                        <th>Sent Date</th>
                        <th>Sent Time</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        sentMails.map(mail => {
                            try {
                                const sd = new Date(mail.sent_date);
                                return (
                                    <tr key={mail.id} >
                                        <td> {mail.from} </td>
                                        <td> {mail.recipient_email} </td>
                                        <td> {mail.subject} </td>
                                        <td> {sd.toLocaleDateString()} </td>
                                        <td> {sd.toLocaleTimeString()} </td>
                                    </tr>
                                )
                            }
                            catch {
                                console.log(mail);
                                return (<> </>)
                            }
                        })
                    }
                </tbody>
            </Table>
        </React.Fragment>

        )
}

function ClickedTable() {
    const [clickedMails, setClickedMails] = useState([{}]);
    const getClicked = async () => {
        axios.get('admin/sentMails/getClicked')
            .then((res) => {
                setClickedMails(res.data);
            });
    }
    useEffect(() => {
        getClicked()
    }, [])

    return (
        <React.Fragment>
            <CSVLink
                data={clickedMails.map((mail) => ({
                    From: mail.from,
                    Recipient: mail.recipient_email,
                    Subject: mail.subject,
                    Sent_Date: (new Date(mail.sent_date)).toLocaleDateString(),
                    Sent_Time: (new Date(mail.sent_date)).toLocaleTimeString(),
                    Clicked_Date: (new Date(mail.clicked_date)).toLocaleDateString(),
                    Clicked_Time: (new Date(mail.clicked_date)).toLocaleTimeString(),
                })
                )}
                filename="Clicked_mails.csv"
            >
                <Button variant="secondary" className="ms-2 mb-2">
                    <i className="bi bi-file-earmark-arrow-down pe-1"></i>Export as CSV
                </Button>
            </CSVLink>

            <Table striped bordered hover >
                <thead>
                    <tr>
                        <th>From</th>
                        <th>Recipient</th>
                        <th>Subject</th>
                        <th>Sent Date</th>
                        <th>Sent Time</th>
                        <th>Clicked Date</th>
                        <th>Clicked Time</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        clickedMails.map(mail => {
                            try {
                                const sd = new Date(mail.sent_date);
                                const cd = new Date(mail.clicked_date);
                                return (
                                    <tr key={mail.id} >
                                        <td> {mail.from} </td>
                                        <td> {mail.recipient_email} </td>
                                        <td> {mail.subject} </td>
                                        <td> {sd.toLocaleDateString()} </td>
                                        <td> {sd.toLocaleTimeString()} </td>
                                        <td> {cd.toLocaleDateString()} </td>
                                        <td> {cd.toLocaleTimeString()} </td>
                                    </tr>
                                )
                            }
                            catch {
                                console.log(mail);
                                return (<> </>)
                            }
                        })
                    }
                </tbody>
            </Table>
        </React.Fragment>

    )
}

function TrainedTable() {
    const [trained, setTrained] = useState([{}]);
    const getTrained = async () => {
        axios.get('admin/sentMails/getTrained')
            .then((res) => {
                setTrained(res.data);
            });
    }
    useEffect(() => {
        getTrained()
    }, [])

    return (
        <React.Fragment>
            <CSVLink
                data={trained.map((mail) => ({
                    Recipient: mail.recipient_email,
                    Sent_Date: (new Date(mail.sent_date)).toLocaleDateString(),
                    Sent_Time: (new Date(mail.sent_date)).toLocaleTimeString(),
                    Training_Date: (new Date(mail.training_date)).toLocaleDateString(),
                    Training_Time: (new Date(mail.training_date)).toLocaleTimeString(),
                })
                )}
                filename="Trained.csv"
            >
                <Button variant="secondary" className="ms-2 mb-2">
                    <i className="bi bi-file-earmark-arrow-down pe-1"></i>Export as CSV
                </Button>
            </CSVLink>

            <Table striped bordered hover >
                <thead>
                    <tr>
                        <th>Recipient</th>
                        <th>Sent Date</th>
                        <th>Sent Time</th>
                        <th>Training Completion Date</th>
                        <th>Training Completion Time</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        trained.map(mail => {
                            try {
                                const sd = new Date(mail.sent_date);
                                const td = new Date(mail.training_date);
                                return (
                                    <tr key={mail.id} >
                                        <td> {mail.recipient_email} </td>
                                        <td> {sd.toLocaleDateString()} </td>
                                        <td> {sd.toLocaleTimeString()} </td>
                                        <td> {td.toLocaleDateString()} </td>
                                        <td> {td.toLocaleTimeString()} </td>
                                    </tr>
                                )
                            }
                            catch {
                                console.log(mail);
                                return (<> </>)
                            }
                        })
                    }
                </tbody>
            </Table>
        </React.Fragment>

    )
}



function Dashboard() {
    const [stats, setStats] = useState({});

    const getStats = async () => {
        axios.get('admin/settings')
            .then((res) => {
                console.log(res.data);
                setStats({
                    totalMails: res.data.total_sent,
                    delivered: res.data.total_sent,
                    clicked: res.data.total_clicked,
                    trained: res.data.total_trained
                });
            })
    }

    useEffect(() => {
        getStats();
    }, []);

    const logout = async () => {
        localStorage.removeItem('token');
        window.location.reload()
    }

    return (
        <div className='container'>
            <span className='container'>
                <h2 className="me-md-auto link-dark text-decoration-none fs-4">
                    <i className="bi bi-speedometer p-2"></i>
                    Dashboard
                    <Button className='float-end ms-2' variant="outline-secondary" onClick={ logout }>
                        <i class="bi bi-box-arrow-right"></i> Logout
                    </Button>
                    <Button className='float-end' variant="outline-secondary" onClick={() => window.location.reload() }>
                        <i class="bi bi-arrow-clockwise"></i>
                    </Button>
                </h2>
            </span>
            <hr />

            <Container className="p-3">
                <Row>
                    <Col>
                        <Alert size="sm" variant="success">
                            <p className="p-1">Training Completed</p>
                            <h3 className="p-1">{stats.trained}/{stats.clicked}</h3>
                        </Alert>
                    </Col>

                    <Col>
                        <Alert variant="danger">
                            <p className="p-1">Links Clicked</p>
                            <h3 className="p-1">{stats.clicked}/{stats.delivered}</h3>
                        </Alert>
                    </Col>

                    <Col>
                        <Alert variant="primary">
                            <p className="p-1">Total Mails Sent</p>
                            <h3 className="p-1">{stats.totalMails}</h3>
                        </Alert>
                    </Col>

                </Row>
            </Container>
            <Container className="p3">
                <Tabs
                    defaultActiveKey="sent"
                    id="mails"
                    className="mb-3"
                >
                    <Tab eventKey="sent" title="Sent mails">
                        <SentTable />
                    </Tab>

                    <Tab eventKey="clicked" title="Clicks">
                        <ClickedTable />                                                 
                    </Tab>

                    <Tab eventKey="trained" title="Training Completed">
                        <TrainedTable />                       
                    </Tab>

                </Tabs>
            </Container>
        </div>
        )
}


export default Dashboard;