import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom'
import Button from 'react-bootstrap/Button';
import pic from './phishing.png';
import video from './video.mp4';
import Modal from 'react-bootstrap/Modal';
import axios from '../axios.js';

function LandingPage() {
    const [client, setClient] = useState("");

    const getClient = async () => {

        axios.get('landingPage/')
            .then((res) => {
                console.log(res.data);
                setClient(res.data.clientName);
            })
    }

    let { click } = useParams();
    const sendClick = async () => {
        
        if (click) {
            console.log("Sending Click")
            axios.post('landingPage/', {id: click})
                .then((res) => {
                    console.log(res.data);
                })
        } else {
            console.log("No Click Id")
        }
    }

    useEffect(() => {
       
        getClient();
        sendClick();
    },)

    const videoCompleted = async () => {
        if (click) {
            axios.post('landingPage/trained', { id: click })
                .then((res) => {
                    console.log(res.data);
                })
        } else {
            console.log("No Click Id")
        }
        console.log("Video Ended");
    }

    const [showVideo, setShowVideo] = useState(false);

    return (
        <React.Fragment>
            <div className="bg-dark">
                <h2 className="fw-light text-light text-center p-3">Welcome to {client}</h2>
            </div>
            <div className="d-flex m-4 p-4">
                <div className="bg-light rounded-3 bg-light m-4 p-4">
                    <h1 className="display-6">You were just <b>phished</b> by your security team as a part of internal simulation.</h1>
                    <p className="lead fs-4 mt-4">It's okay! You are a human.Let's learn from this.</p>
                    <hr className="my-4" />
                    <p className="fs-5">A real attack could have put our organization and our customer at risk! </p>
                    <p className="lead fs-5 mt-4">We recommend you to complete the training by watching this short video.</p>
                    <Button className="" variant="dark" onClick={() => { setShowVideo(true) }}>Watch Now</Button>                   
                </div>
                <img src={pic} alt="Beware of Phishing" height={400} />
            </div>

            <div className="bg-light fixed-bottom pt-2 text-right">
                <p className="text-center"> Powered by Apptriangle Limited</p>
            </div>

            <Modal
                show={showVideo}
                onHide={() => setShowVideo(false)}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Phishing Awareness
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <video className="p-2" width="750" controls onEnded={videoCompleted } >
                        <source src={video} type="video/mp4" />
                    </video>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="outline-dark" onClick={() => setShowVideo(false)}>Close</Button>
                </Modal.Footer>
            </Modal>

         </React.Fragment>
        )
}

export default LandingPage;