import React, { useState, useRef } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Row from 'react-bootstrap/Row';
import { Editor } from '@tinymce/tinymce-react';
import Modal from 'react-bootstrap/Modal';
import axios from '../axios.js';

function EmailSentModal(props) {
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    {props.sent ? "Email Sent!" : "Error"}{props.sent}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {props.sent ?
                    (<React.Fragment>
                        <h4>Email was sent successfully</h4>
                        <p className="ms-4">
                            <div dangerouslySetInnerHTML={{ __html: props.email.message }} />
                        </p>
                    </React.Fragment>
                    ) :  (<React.Fragment>
                            <p> An error occured while trying to send the email. Please check all fields and try again later. </p>
                           </React.Fragment>
                         )
                }
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={props.onHide}>Close</Button>
            </Modal.Footer>
        </Modal>
    );
}


function MailForm() {
    const [email, setEmail] = useState({
        bulk: false,
        message: ""
    });
    const [validated, setValidated] = useState(false);
    const [disableSend, setDisableSend] = useState(false);

   /* const getEmail = async () => {
        let email = DummyData.email;
        await Promise.resolve("hi")
            .then(setEmail(email));
    }

    useEffect(() => {
        getEmail();
    }, [])*/

    const handleChange = (e) => {
        const value =
            e.target.name === "tls" || e.target.name === "bulk"
                ? e.target.checked : e.target.value;
        console.log(e.target.name, value);
        setEmail((email) => {
            return {
                ...email,
                [e.target.name]: value
            }
        })
        console.log(email)
    }

    
    const handleFileChange = (e) => {
        console.log(e.target.files[0]);
        setEmail((email) => {
            return {
                ...email,
                [e.target.name]: e.target.files[0]
            }
        })
        console.log(email)
    }

    const handleRecipients = (e) => {
        var emails = e.target.value;

        // Split string by comma into an array
        emails = emails.split(",");

        var valid = true;
        var regex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        var invalidEmails = [];

        for (var i = 0; i < emails.length; i++) {
            // Trim whitespaces from email address
            emails[i] = emails[i].trim();

            // Check email against our regex to determine if email is valid
            if (emails[i] == "" || !regex.test(emails[i])) {
                invalidEmails.push(emails[i]);
            }
        }
        setEmail({
            ...email,
            to: emails
        })
        console.log(email);
    }

    const [modalShow, setModalShow] = useState(false);
    const [sentSuccessful, setSentSuccessful] = useState(false);

    const handleSubmit = async (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
            window.alert("Error! Please make sure all mandatory fields are correct")
            console.log("error! Could not submit")
        } else {
            setValidated(true);
            setDisableSend(true);
            setEmail((email) =>{
                return {
                    ...email,
                    message: editorRef.current.getContent()
                }
            })
            console.log("Email:");
            console.log(email);

            const formData = new FormData();
            formData.append('name', email.name);
            formData.append('address', email.from);
            formData.append('to', email.to);
            formData.append('subject', email.subject);
            formData.append('html', editorRef.current.getContent());
            formData.append('bulk', email.bulk);
            formData.append('attachment', email.attachment);
            formData.append('csv', email.csv);
            console.log("form data: ", formData);
            
            axios.post('admin/sentMails/new', formData)
                .then((res) => {
                    setDisableSend(false);
                    console.log(res);
                    if (res.status === 201) {
                        setSentSuccessful(true);
                        setModalShow(true);
                    } else {
                        throw new Error("Invalid Settings");
                    }
                })
                .catch(() => {
                    setSentSuccessful(false);
                    setModalShow(true);
                    setDisableSend(false);
                });

        }

    };

    const editorRef = useRef(null);
    const log = () => {
        if (editorRef.current) {
            console.log(editorRef.current.getContent());
        }
    };

    return (
        <React.Fragment>
         <h2 className="me-md-auto link-dark text-decoration-none fs-4">
                <i className="bi bi-envelope p-2"></i>
                Compose Email
            </h2>
            <hr />

            <Form noValidate validated={validated} encType="multipart/form-data" onSubmit={handleSubmit}>

            <Form.Group as={Row}  className="p-2" controlId="validationCustom01">
                <Form.Label column md={2}>From</Form.Label>
                <Col >
                    <InputGroup hasValidation>
                        <Form.Control
                            required
                            type="email"
                            placeholder="From"
                            name="from"
                            defaultValue={email.from}
                            onChange={handleChange}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter a valid Sender e-mail address.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
             </Form.Group>

                <Form.Group as={Row} className="p-2" controlId="name">
                    <Form.Label column md={2}>Sender's Name</Form.Label>
                    <Col>
                        <InputGroup hasValidation>
                            <Form.Control
                                required
                                type="text"
                                placeholder="From"
                                name="name"
                                defaultValue={email.senderName}
                                onChange={handleChange}
                            />
                            <Form.Control.Feedback type="invalid">
                                Please enter a valid Sender e-mail address.
                            </Form.Control.Feedback>
                        </InputGroup>
                    </Col>
                </Form.Group>

            <Form.Group as={Row} className="p-2" controlId="validationCustom02">
                <Form.Label column md={2}>To</Form.Label>
                <Col>
                    <InputGroup hasValidation>
                        <Form.Control
                            required={!email.bulk}
                            disabled={email.bulk}
                            type="email"
                            placeholder="Recipient"
                            name="to"
                            defaultValue={email.to}
                            onChange={handleRecipients}
                            multiple
                        />
                        <Form.Control.Feedback type="invalid">
                            Please check valid recipient addresses.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
            </Form.Group>

            <Form.Group as={Row} className="p-2" controlId="bulk">
                <Col md={{offset: 2 }}>
                    <Form.Check
                        type="switch"
                        id="bulk"
                        label="Send Bulk Mail"
                        name="bulk"
                        defaultChecked={email.bulk}                       
                        onClick={handleChange}
                    />
                </Col>
            </Form.Group>

            <Form.Group as={Row} className="p-2 mb-3" controlId="formFile" hidden={!email.bulk} >
                <Form.Label column md={2} >Upload CSV file</Form.Label>
                    <Col>
                        <InputGroup hasValidation>
                            <Form.Control
                                required={email.bulk}
                                type="file"
                                name="csv"
                                disabled={!email.bulk}
                                accept=".csv"
                                onChange={handleFileChange}

                            />
                            <Form.Control.Feedback type="invalid">
                                Please Upload a .csv file
                            </Form.Control.Feedback>
                        </InputGroup>
                </Col>
            </Form.Group>

            <Form.Group as={Row} className="p-2" controlId="validationCustomSubject">
                <Form.Label column md={2}>Subject</Form.Label>
                <Col>
                    <InputGroup hasValidation>
                        <Form.Control
                            required
                            type="text"
                            placeholder="Subject"
                            name="subject"
                            defaultValue={email.subject}
                            onChange={handleChange}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter Subject.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Col>
            </Form.Group>

            <Form.Group as={Row} className="p-2 mb-3" controlId="attachment" >
                <Form.Label column md={2} >Attachment</Form.Label>
                <Col>
                        <Form.Control
                            type="file"
                            name="attachment"
                            onChange={handleFileChange}
                        />
                </Col>
            </Form.Group>

            <Form.Group as={Row} md="4" className="p-2" controlId="validationCustom03">
                <Form.Label column md={2}>Message</Form.Label>
                <Col md={10}>
                    <InputGroup hasValidation>

                        

                    <Editor
                        apiKey='g83ywjl07a5elmc55icdie0aqhyz26l0sqkyyqgwoogjtvpy'
                        onInit={(evt, editor) => editorRef.current = editor}
                        initialValue="<p>This is the initial content of the editor.</p>"
                        init={{
                            height: 500,
                            menubar: false,
                            plugins: [
                                'advlist', 'autolink', 'lists', 'link', 'image', 'charmap', 'preview',
                                'anchor', 'searchreplace', 'visualblocks', 'code', 'fullscreen',
                                'insertdatetime', 'media', 'table', 'code', 'help', 'wordcount'
                            ],
                            toolbar: 'undo redo | blocks | link ' +
                                'bold italic forecolor | alignleft aligncenter ' +
                                'alignright alignjustify | bullist numlist outdent indent | ' +
                                'removeformat | help',
                            content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }'
                        }}
                    />

                    <Form.Control.Feedback type="invalid">
                        Please enter Message.
                    </Form.Control.Feedback>
                </InputGroup>
                </Col>
            </Form.Group>
         

                <Button className="" type="submit" disabled={disableSend}>Send</Button>
            </Form>

            <EmailSentModal
                sent={sentSuccessful}
                email={email}
                show={modalShow}
                onHide={() => setModalShow(false)}
            />

            </React.Fragment>
    );
}

function ComposeMail() {
    return (
        <MailForm />
    )
}


export default ComposeMail;