import React, { useState, useEffect, useRef } from 'react';
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import axios from '../axios.js';

import Sidebar from '../components/Sidebar/sidebar';

import Dashboard from './dashboard';
import ComposeMail from './composeMail';
import Settings from './settings';
import LandingPage from './landingPage';

function Admin() {
    const [loggedIn, setLoggedIn] = useState(false);
    const [loginForm, setLoginForm] = useState({
        user: '',
        password: ''
    })
    const handleLogin = (e) => {
        const { name, value } = e.target;
        console.log(name, value);
        setLoginForm((prev) => {
            return {
                ...prev,
                [name]: value
            }
        })
    }

    const login = async (event) => {
        event.preventDefault();
        axios.post('/admin/login', loginForm)
            .then((res) => {
                if (res.status === 200) {
                    localStorage.setItem('token', res.data.token)
                    setLoggedIn(true);
                } else {
                    window.alert(res.data.Message);
                }
            })
            .catch((err) => {
                window.alert("Invalid Credentials")
            })

    }
    useEffect(() => {
        if (localStorage.getItem('token')) {
            axios.post('/admin/login', { token: localStorage.getItem('token') })
                .then((res) => {
                    if (res.status === 200) {
                        setLoggedIn(true);
                    } else {
                        localStorage.setItem('token', '');
                    }
                })
        }
    }, [])

    if (loggedIn) {
        return (
            <div style={{ display: "flex" }}>
                <Sidebar />
                <Container className="p-3" style={{ marginLeft: "20%" }}>
                    <Container className="p-5 mb-4 bg-light rounded-3">
                        <Routes>
                            <Route path="" element={<Dashboard />} />

                            <Route path="composemail" element={<ComposeMail />} />

                            <Route path="settings" element={<Settings />} />

                            <Route path="landingpage" element={<LandingPage />} />

                        </Routes>
                    </Container>
                </Container>
            </div>
        )
    } else {
        return (
            <div className="bg-light rounded-4 p-4 login-form">
                {/*<Form noValidate validated={validated} onSubmit={handleSubmit}> */}
                <Form>
                    <Form.Group md="4" className="pt-2" controlId="validationCustomUsername">
                        <Form.Label column md={2}>Username</Form.Label>
                            <Col>
                                <InputGroup hasValidation>
                                    <Form.Control
                                    required
                                    type="text"
                                    placeholder="Username"
                                    name="user"
                                    onChange={handleLogin }

                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter username.
                                    </Form.Control.Feedback>
                                </InputGroup>
                            </Col>
                    </Form.Group>

                    <Form.Group md="4" className="pt-2" controlId="validationCustom03">
                        <Form.Label column md={2}>Password</Form.Label>
                            <Col>
                                <InputGroup hasValidation>
                                    <Form.Control
                                        required
                                        type="password"
                                        placeholder="Password"
                                        name="password"
                                        onChange={handleLogin} 

                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter password.
                                    </Form.Control.Feedback>
                                </InputGroup>
                            </Col>
                    </Form.Group>

                    <Button className="mt-3" variant="dark" onClick={login}>Login</Button>
                </Form>

            </div>

            )
    }
};

export default Admin;