import React, { useState, useEffect } from 'react';
import {Link } from 'react-router-dom';
import './sidebar.css';
import axios from '../../axios.js';

const companyName = "App Triangle"

function Sidebar() {

    const [client, setClient] = useState({});
    const getClient = async () => {
        axios.get('admin/settings')
            .then((res) => {
                console.log(res.data);
                setClient({ name: res.data.clientName });
            })


    }

    useEffect(() => {
        getClient();
    }, [])

    return (

        <span className="d-flex flex-column flex-shrink-0 p-3 bg-light sidebar" >

            <a href="/" className="d-flex align-items-center mb-3 mb-md-0 me-md-auto link-dark text-decoration-none fs-4 m-4">
                {client.name}
            </a>
            <ul className="nav nav-pills flex-column mb-auto mt-4 p-1">
                <li className="sidebar-item">
                    <Link to="/admin" className="nav-link link-dark">
                        <i className="bi bi-speedometer p-2"></i>
                        Dashboard
                    </Link>
                </li>
                <li className="sidebar-item">
                    <Link to="/admin/composemail" className="nav-link link-dark">
                        <i className="bi bi-envelope p-2"></i>
                        Compose Mail
                    </Link>
                </li>
                <li className="sidebar-item">
                    <Link to="/admin/settings" className="nav-link link-dark">
                        <i className="bi bi-sliders p-2"></i>
                        Settings
                    </Link>
                </li>
                <li className="sidebar-item">
                    <Link to="/admin/landingpage" className="nav-link link-dark">
                        <i className="bi bi-house p-2"></i>
                        Landing Page Preview
                    </Link>
                </li>
            </ul>
        </span>

    )
};

export default Sidebar;